ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
img{
    width: 100%;
    height: 100%;

}


.backdrop {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.56);
}

.modal-container {
    position: fixed;
    width: 280px;

    top: 50%;
    left: 50%;

    display: block;
    transform: translate(-50%, -50%) scaleY(1);

    border: 1px solid #000000;
    background-color: white;
    border-radius: 5px;
    
    padding: 48px 16px;

    overflow-x: hidden;
    @media screen and (min-width: 480px) {
        width: 380px;
    }

    @media screen and (min-width: 768px) {

        top: 50%;
        left: 50%;
        width: 700px;
        display: flex;
        min-height: 420px;
        max-height:550px ;
        padding: 40px 16px;

        overflow-y: hidden;
    }

    @media screen and (min-width: 1024px) {
        width: 815px;
        height: 560px;
    }
}


.cross {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 40px;
    color: rgb(113, 110, 105);
    border: none;
    background-color: transparent;

    position: absolute;
    z-index: 1;
    top: 0%;
    right: 1%;
    transform: rotate(45deg);

    transition: color 250ms linear;

    &:hover {
        color: orange;
    }
}
.modal-img-container{
    
    
    margin-bottom: 20px;
    @media screen and (min-width: 480px) {
        margin-left: 54px;
        margin-right: 54px;
    }
    @media screen and (min-width: 768px) {
        margin-right: 68px;
        margin-left: 36px;
        margin-bottom: 0;
         }
    @media screen and (min-width: 1024px) {  
        margin-right: 45px;
        margin-left: 29px;
     }  
    

}

.modal-img {
    width: 240px;
    height: 357px;
    border-radius: 5px;

    @media screen and (min-width: 480px) {
            margin-left: auto;
            margin-right: auto;
        }

    @media screen and (min-width: 768px) {
        max-width: 280px;
        max-height: 373px;
    }
    
    @media screen and (min-width: 1024px) {
       width: 100%;
       height: 100%;
       max-height: 478px;
       min-width: 300px;
    }
}



.film-name {
    position: relative;
    margin: 0;
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 30px;
    line-height: 35px;
    
    color: #000000;
}





.film-description {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;  
    width: 100%;
    max-height: 240px;
    color: #000000;
    overflow-y: scroll;
    @media screen and (min-width: 768px){
        max-height: 200px;
    }
    
    @media screen and (min-width: 1024px) {
        max-height: 160px;
    }
}

.modal-btn-wrap {
display: flex;
justify-content: center;
@media screen and (min-width: 768px){
    justify-content: flex-start;
}


}

.modal-btn:first-child {
    margin-right: 15px;
}

.modal-btn {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    padding: 12px 10px 12px;
    border-radius: 5px;
    background: #FFFFFF;
    color: #000000;
    border: 1px solid #000000;
    cursor: pointer;

    transition: background-color  $transition-timing $transition-function,
    color  $transition-timing $transition-function,
    border  $transition-timing $transition-function;;

    &:hover {
    background-color: #FF6B01;
    color: white;
    border: 1px solid transparent;
    }

    &.watched,
    &.queued{
        color: #282828;
        background-color: #a7cf3bcc;
        border: 1px solid transparent;
    }
}

.modal-btn--active {
    background-color: #FF6B01;
    color: white;
    border: none;
}
.modal-table{
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    &__attribute{
        margin-top: 0;
        margin-bottom: 8px;
        margin-right: 40px;
        color: #8C8C8C;
        white-space: nowrap
    
    }
    &__value{
        margin-top: 0;
        margin-bottom: 8px;
    }   
    &__vote{
        background: #FF6B01;
        border-radius: 5px;
        color: #FFFFFF;
        padding-left: 5px;
        padding-right: 5px;
        margin-right: 1px;

    }
    &__votes{
        background: #F7F7F7;
        border-radius: 5px;
        padding-left: 5px;
        padding-right: 5px;
        margin-left: 1px;
    }
    

}

.film-about{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.33;

}
